        idParam$
            .filter(function (id) { return id !== 'current'; })
            .map(function (idString) { return Number(idString); })
            .switchMap(function (budgetId) { return _this.budgetService.getBudget(budgetId); })
            .subscribe(function (budget) {
            _this.budget = budget;
            _this.updateHeading();
        });
